@keyframes rotate-anticlockwise {
  0%, 100% {
    opacity: 1;
    transform: scaleX(1);
  }

  50% {
    opacity: .5;
    transform: scaleX(-1);
  }
}

@keyframes rotate-clockwise {
  0%, 100% {
    opacity: 1;
    transform: scaleX(-1);
  }

  50% {
    opacity: .5;
    transform: scaleX(1);
  }
}

* {
  margin: 0;
}

:root {
  background: linear-gradient(90deg, #fdbb2d 0%, #3a1c71 100%);
}

.tile {
  opacity: 0;
  object-fit: cover;
  stroke: rgba(var(--black), .7);
  stroke-width: .03px;
  width: 2%;
  transition: stroke .1s;
  animation: 3s 2 forwards rotate-anticlockwise;
}

.tile > path {
  stroke: inherit;
  stroke-width: inherit;
}

.is-inverted {
  animation: 2s 3 forwards rotate-clockwise;
  transform: scaleX(-1);
}

.row {
  display: flex;
}

.is-row-inverted {
  transform: scaleY(-1);
}

